:root{
    --var-fundoTextos:rgb(214, 255, 138);
}

.textos{
    display: flex;
    flex-direction: row;
    width: 90vw;
    height: 50vh;
    justify-self: center;
    border-radius: 30px;
    border: #000 solid 2px;
    background-color: var(--var-fundoTextos);
    margin-top: 100px;
}

.TextoCabeca{
    position: relative;
    bottom: 25%;
    left: 25px;
}
.TextoCabeca h1{
    font-size: 45px;
    background-color: rgb(190, 255, 162);
    padding: 10px;
    border-radius: 40px;

    border-right: rgb(90, 155, 62) solid 5px;
    border-bottom: rgb(90, 155, 62) solid 5px;
}

.textin{
    position: absolute;
    margin-top: 2%;
    margin-left: 2%;
    width: 88%;
    text-align: center;
    align-content: center;
}
.textin p{
    font-size: 30px;
}


.textosCel{
    display: flex;
    flex-direction: row;
    width: 90vw;
    height: 25vh;
    justify-self: center;
    border-radius: 30px;
    border: #000 solid 2px;
    background-color: var(--var-fundoTextos);
    margin-top: 70px;
}
.TextoCabecaCel{
    position: relative;
    bottom: 25%;
    left: 25px;
}
.TextoCabecaCel h1{
    font-size: 30px;
    background-color: rgb(190, 255, 162);
    padding: 10px;
    border-radius: 40px;

    border-right: rgb(90, 155, 62) solid 5px;
    border-bottom: rgb(90, 155, 62) solid 5px;

}

.textinCel{
    position: absolute;
    margin-top: 10%;
    margin-left: 2%;
    width: 88%;
    text-align: center;
    align-content: center;
}
.textinCel p{
    font-size: 20px;
}